@import '../../Global.scss';

.promocion{
    margin-top: 0;
    margin-bottom: 2rem;
    padding: 0;
    &_header{
        background: $color-secundario;
        padding: 1rem;
        margin-bottom: 1rem;
        text-align: center;
        h2{
            margin: 1rem auto;
            width: 15rem;
            font-weight: bold;
            text-transform: uppercase;
        }
        h3{
            display: inline-block;
            border-radius: 2rem;
            padding: .3rem 1rem;
            background-color: $color-blanco;
            font-size: 1.2rem;
            span{
                font-weight: bold;
                color: red;
            }
        }
    }

    &_listado{
        
        p{  color: $color-negro;
            font-weight: bold;
            margin-left: 2rem;
        }
        ul{
            display: flex;
            flex-direction: column;
            span{
                font-weight: bold;
            }           
        }
       
    }
 
}
@media (min-width: 768px){
    .promocion_listado{
        padding-left: 2rem;
    }
    .promocion_header h2{
        width: 20em;
    }
}

.solicitud{
    font-size: 1.2rem;
    font-weight: 500;
    span{
        color: $color-primario;
    }
    
}
.testimonios{
    &_famosos{
        justify-content: center;
    }
   
}

article{
    margin: 1rem 1rem;
    width: 10rem;
    padding: .5rem;
    text-align: center;
    img{
        width: 8rem;
        border: 1px solid rgb(226, 226, 226);
        border-radius: 50%;
        margin-bottom: .5rem;
    }
    i{
        font-size: 6rem;
        margin-bottom: 2rem;
        color: $color-oscuro;

    }
    h4{
        font-size: 1rem;
        margin: 0;
    }
    span{
        margin-top: 0;
        font-size: .8rem;
        color:rgb(172, 172, 172);
    }
    p{
        font-style: oblique;
        margin-top: 1rem;

    }
}

.promocion_footer{
    text-align: center;
    p{
        font-size: 1.2rem;
        font-weight: bold;
        span{
            color: red;
        }
    }
}