@import '../../Global.scss'; 

.seccion{
    margin-top: 2rem;
    margin-bottom: 2rem;
}
.conoceme{
    font-weight: 300;
    margin-top: 2rem;
    margin-bottom: 2rem;
}
.card{
    width: 18rem;
    margin-bottom: 3rem;
    transition: .5s;
    h5{
        color: $color-oscuro;
       }
    p{
        color: $color-oscuro;
       }
 
}
.card:hover{
    background-color: $color-secundario;
    box-shadow: -2px -2px 5px #cecece;
    cursor: pointer;
    
} 



.card:active{
    background-color: $color-blanco;
}

.sobreMi{
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;

    img{
        width: 8rem;
        height: 8rem;
        border: 1px solid rgb(226, 226, 226);
        border-radius: 50%;
        margin-bottom: .5rem;
    }
    h5{
        span{
            font-size: 1rem;
            color: #929191;
        }
    }
    p{
        color: #929191 ;
        span{
            font-weight: bold;
        }
    }
}
.avatar{
    text-align: center;
}

 @media (min-width: 768px) {
   .sobreMi{
       flex-direction: row;
   }
   .avatar{
    text-align: start;
}
} 

.aling{
    align-items: center;
}
.redes{
    text-align: center;
    font-size: 2rem;
    
    a{
        color: $color-oscuro;
        i{
        margin-right: 1rem;
    }
    }
    .fa-instagram:hover{
     color: #9e1197;
    }
    .fa-linkedin:hover{
        color: #1012b6;
    }
    .fa-whatsapp:hover{
        color: #119e29;
    }
}