body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.g-area{align-items:center;display:grid;grid-template-rows:repeat(3, 2.7rem);grid-template-columns:repeat(1, 1fr);grid-template-areas:"glogo" "gtexto" "gblog"}@media (min-width: 768px){.g-area{grid-template-columns:repeat(5, 1fr);grid-template-rows:3rem;grid-template-areas:"glogo gtexto gtexto gtexto gblog"}}.glogo{grid-area:glogo}.gtexto{grid-area:gtexto}.gblog{grid-area:gblog}.nav{padding-bottom:1rem;background-color:#28292D;text-align:center;justify-content:center}.nav_logo{font-weight:bold;color:#fff}.nav_logo:hover{text-decoration:none;color:#fff}.nav_text{margin:0;text-align:center;text-transform:uppercase;color:#FDD55B;font-weight:bold}.nav_blog{display:inline-block;text-align:center}@media (min-width: 768px){.nav{padding-bottom:0}}

.promocion{margin-top:0;margin-bottom:2rem;padding:0}.promocion_header{background:#FDD55B;padding:1rem;margin-bottom:1rem;text-align:center}.promocion_header h2{margin:1rem auto;width:15rem;font-weight:bold;text-transform:uppercase}.promocion_header h3{display:inline-block;border-radius:2rem;padding:.3rem 1rem;background-color:#fff;font-size:1.2rem}.promocion_header h3 span{font-weight:bold;color:red}.promocion_listado p{color:#28292D;font-weight:bold;margin-left:2rem}.promocion_listado ul{display:flex;flex-direction:column}.promocion_listado ul span{font-weight:bold}@media (min-width: 768px){.promocion_listado{padding-left:2rem}.promocion_header h2{width:20em}}.solicitud{font-size:1.2rem;font-weight:500}.solicitud span{color:#23A6DA}.testimonios_famosos{justify-content:center}article{margin:1rem 1rem;width:10rem;padding:.5rem;text-align:center}article img{width:8rem;border:1px solid #e2e2e2;border-radius:50%;margin-bottom:.5rem}article i{font-size:6rem;margin-bottom:2rem;color:#0E0E44}article h4{font-size:1rem;margin:0}article span{margin-top:0;font-size:.8rem;color:#acacac}article p{font-style:oblique;margin-top:1rem}.promocion_footer{text-align:center}.promocion_footer p{font-size:1.2rem;font-weight:bold}.promocion_footer p span{color:red}

.seccion{margin-top:2rem;margin-bottom:2rem}.conoceme{font-weight:300;margin-top:2rem;margin-bottom:2rem}.card{width:18rem;margin-bottom:3rem;transition:.5s}.card h5{color:#0E0E44}.card p{color:#0E0E44}.card:hover{background-color:#FDD55B;box-shadow:-2px -2px 5px #cecece;cursor:pointer}.card:active{background-color:#fff}.sobreMi{display:flex;flex-direction:column;margin-bottom:1rem}.sobreMi img{width:8rem;height:8rem;border:1px solid #e2e2e2;border-radius:50%;margin-bottom:.5rem}.sobreMi h5 span{font-size:1rem;color:#929191}.sobreMi p{color:#929191}.sobreMi p span{font-weight:bold}.avatar{text-align:center}@media (min-width: 768px){.sobreMi{flex-direction:row}.avatar{text-align:start}}.aling{align-items:center}.redes{text-align:center;font-size:2rem}.redes a{color:#0E0E44}.redes a i{margin-right:1rem}.redes .fa-instagram:hover{color:#9e1197}.redes .fa-linkedin:hover{color:#1012b6}.redes .fa-whatsapp:hover{color:#119e29}

.navbar{color:#fff;align-items:center;justify-content:center;padding:.5rem 0;margin-bottom:0}.navbar_logo{font-weight:bold}.navbar_text{text-align:center;text-transform:uppercase;color:#FDD55B;font-weight:bold}

h1{text-align:center;font-size:2rem;margin-bottom:2rem}p span{font-weight:bold}.container-blog{text-align:justify;max-width:90%;margin:0 auto}@media (min-width: 768px){.container-blog{max-width:540px}}@media (min-width: 980px){.container-blog{max-width:768px}}

