h1{
    text-align: center;
    font-size: 2rem;
    margin-bottom: 2rem;
}

p{
    span{
        font-weight: bold;
    }
}
.container-blog{
    text-align: justify;
    max-width: 90%;
    margin: 0 auto;
}


@media (min-width: 768px) {
    .container-blog{
        max-width: 540px;
    }
}

@media (min-width: 980px) {
    .container-blog{
        max-width: 768px;
    }
}