@import '../../Global.scss';

.g-area{
  align-items: center;
  display: grid;
  grid-template-rows: repeat(3,2.7rem);
  grid-template-columns: repeat(1,1fr);
  grid-template-areas: 
  "glogo"
  "gtexto"
  "gblog";
}

@media (min-width: 768px) {
  .g-area{
    grid-template-columns: repeat(5, 1fr); // -->
    grid-template-rows: 3rem;  // abajo
    grid-template-areas: 
    "glogo gtexto gtexto gtexto gblog";
  }  
}
.glogo{
  grid-area: glogo;
}
.gtexto{
  grid-area: gtexto;
}
.gblog{
  grid-area: gblog;
}

.nav{
  padding-bottom: 1rem;
  background-color: $color-negro;
  text-align: center;
  justify-content: center;
  &_logo{
    font-weight: bold;
    color: $color-blanco;
  }
  &_logo:hover{
   text-decoration: none;
    color: $color-blanco;
  }
  &_text{
    margin: 0;
    text-align: center;
    text-transform: uppercase;
    color: $color-secundario;
    font-weight: bold;
  }
  &_blog{
    display: inline-block;
    text-align: center;
  }
}

@media (min-width: 768px) {
  .nav{
    padding-bottom: 0;
  }
  
}